import React from 'react';
import BasicTable from './Basic';
import CrossTable from './Cross';
import './index.scss';
import { ExternalRenderProps } from '@xmly/rocket-chart/lib/types';

const Table: React.FC<ExternalRenderProps> = ({ chartOptions, container, chartProps }) => {
  const { device, theme, type } = chartProps;
  const paddingTop = device === 'mobile' ? 4 : 12;
  const componentProps = {
    tableProps: chartOptions.table || {},
    container,
    theme,
    device,
  };
  return (
    <div
      className={`rocketChart-table rocketChart-table-${theme} rocketChart-table-${device}`}
      style={{ paddingTop }}
    >
      {type.includes('cross') ? (
        <CrossTable {...componentProps} />
      ) : (
        <BasicTable {...componentProps} />
      )}
    </div>
  );
};

export default Table;
